.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 50px;
  background-color: #202020;
  color: #ffffff;
}

.input-field {
  margin: 20px 0;
  padding: 10px 20px;
  width: 100%;
  max-width: 300px;
  font-size: 18px;
  background-color: #303030;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  transition: all 0.3s ease;
}

.input-field:focus {
  box-shadow: 0 0 10px 2px rgba(255, 255, 255, 0.1);
  background-color: #404040;
}

.button,
.copy-button {
  margin: 20px 0;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #6c757d;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.button:hover,
.copy-button:hover {
  background-color: #5a6268;
  transform: scale(1.05);
}

.result {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 300px;
  font-size: 24px;
  background-color: #303030;
  padding: 20px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.result p {
  margin: 0;
}

.copy-button {
  background-color: #007bff;
}

.copy-button:hover {
  background-color: #0056b3;
}
